@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
  max-height: 100vh;
  max-width: 100vw;
}

.app-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 5fr 1fr;
}

nav {
  grid-column: 1/3;
  grid-row: 1/2;
  min-height: 5vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-bottom: 1px solid black;
}

.nav-item {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

main {
  display: flex;
  justify-content: center;
  grid-column: 1/3;
  grid-row: 2/3;
  min-height: 90vh;
}

footer {
  grid-column: 1/3;
  grid-row: 3/4;
  min-height: 5vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-top: 1px solid black;
}

.footer-item {
  text-decoration: none;
  color: black;
}

.favorites-track-list {
  list-style: none;
}
.home-container {
    text-align: center;
    margin-top: 3rem;
}

.home-header {
    margin-bottom: 3rem;
    font-size: 4.5rem;
}

.spot-header {
    color: gray;
}

.home-form-container {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 5rem
}

.username-input {
    border: none;
    outline: 1px solid black;
    padding: 0.25rem;
}

.username-input:-ms-input-placeholder {
    color: black;
}

.username-input::placeholder {
    color: black;
}

.password-input {
    border: none;
    outline: 1px solid black;
    padding: 0.25rem;
}

.password-input:-ms-input-placeholder {
    color: black;
}

.password-input::placeholder {
    color: black;
}

.login-btn {
    border: none;
    outline: 1px solid black;
    padding: 0.25rem;
}

.login-btn:hover {
    cursor: pointer;
}

.sign-up-btn {
    border: none;
    outline: 1px solid black;
    padding: 0.25rem;
}

.sign-up-btn:hover {
    cursor: pointer;
}

@media screen and (min-width: 688px) {
    .home-form-container {
        margin: 0 15rem;
    }
}

@media screen and (min-width: 992px) {
    .home-form-container {
        margin: 0 25rem;
    }
}

@media screen and (min-width: 1296px) {
    .home-form-container {
        margin: 0 35rem;
    }
}
.generator-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr, 0.5fr, 5fr 3fr;
    font-size: 0.75rem;
}

.new-playlist-form {
    grid-column: 1/3;
    grid-row: 2/3;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.checkbox-form-container {
    grid-column: 1/3;
    grid-row: 1/2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.checkbox-container input {
    margin: 0.25rem;
}

.new-playlist-input:-ms-input-placeholder {
    color: black;
}

.new-playlist-input::placeholder {
    color: black;
}

.generator-btn {
    min-width: 50vw;
}

.generator-btn:-ms-input-placeholder {
    color: black;
}

.generator-btn::placeholder {
    color: black;
}

.save-playlist-btn {
    margin-top: 1rem;
}

.generated-playlist-container {
    grid-column: 1/3;
    grid-row: 3/4;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    min-width: 80vw;
    max-width: 80vw;
}

.generated-playlist {
    display: flex;
    flex-direction: column;
    list-style: none;
}

.generated-playlist li {
    border: 1px solid black;
    padding: 0.25rem;
}

.album-artwork {
  display: none;
}

.add-to-playlist-list {
    grid-column: 1/3;
    grid-row: 4/5;
    display: flex;
    grid-gap: 0.25rem;
    gap: 0.25rem;
    flex-wrap: wrap;
    list-style: none;
}

.add-to-playlist-list li {
    margin-right: 1rem;
}

@media screen and (max-width: 414px) {
    .checkbox-container {
        font-size: 0.70rem;
    }
    .generator-btn {
        font-size: 0.70rem;
    }
    .new-playlist-input {
        font-size: 0.70rem;
    }
    .save-playlist-btn {
        font-size: 0.70rem;
    }
    .generated-playlist {
        font-size: 0.70rem;
    }
    .add-to-playlist-list {
        font-size: 0.70rem;
    }
    .song-btns {
        font-size: 0.70rem;
    }
}

@media screen and (min-width: 600px) {
    .checkbox-container {
        font-size: 1.0rem;
    }
    .generator-btn {
        font-size: 1.0rem;
    }
    .new-playlist-input {
        font-size: 1.0rem;
    }
    .save-playlist-btn {
        font-size: 1.0rem;
    }
    .generated-playlist {
        font-size: 1.0rem;
    }
    .add-to-playlist-list {
        font-size: 1.0rem;
    }
    .new-playlist-form {
        margin: 0 15rem;
    }
    .generator-btn {
        min-width: 25vw;
    }
}

@media screen and (min-width: 992px) {
    .new-playlist-form {
        margin: 0 20rem;
    }
    .generator-btn {
        min-width: 32vw;
    }
}
.favorites-list {
    margin-top: 0.5rem;
}

.favorites-container {
    margin-top: 0.5rem;
    text-align: center;
}

@media screen and (max-width: 414px) {
    .favorites-list {
        font-size: 0.70rem;
    }
}

@media screen and (min-width: 414px) {
    .favorites-list {
        font-size: 0.70rem;
    }
}

@media screen and (min-width: 600px) {
    .favorites-list {
        font-size: 0.9rem;
    }
}

.playlist-container {
    margin: 2rem;
    text-align: center;
}

.single-playlist {
    margin: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 0.25rem;
    gap: 0.25rem;
}

.playlist-headers {
    text-align: center;
    margin: 1rem;
}

.song-list-container {
    text-align: center;
}

@media screen and (max-width: 414px) {
    .single-playlist {
        font-size: 0.80rem;
    }
    .single-playlist-btns {
        font-size: 0.70rem;
    }
    .song-list-container {
        font-size: 0.70rem;
    }
}

@media screen and (min-width: 600px) {
    .single-playlist {
        font-size: 1.0rem;
    }
    .single-playlist-btns {
        font-size: 1.0rem;
    }
    .song-list-container {
        font-size: 1.0rem;
    }
}

.info-container {
    display: flex;
    min-height: 90vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.info-paragraph {
    flex-grow: 0.75;
    text-align: center;
    margin: 0 2rem;
    font-size: 1.5rem;
    line-height: 2;
}
.team-container {
    display: flex;
    min-height: 90vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.team-paragraph {
    flex-grow: 0.75;
    text-align: center;
    margin: 0 2rem;
    font-size: 1.5rem;
    line-height: 2;
}
.legal-container {
    display: flex;
    min-height: 90vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.legal-paragraph {
    flex-grow: 0.75;
    text-align: center;
    margin: 0 2rem;
    font-size: 1.5rem;
    line-height: 2;
}
